import Vue from 'vue'
import VueRouter from 'vue-router'
import AddBlog from './components/AddBlog'
import ShowBlogs from './components/ShowBlogs'
import SingleBlog from './components/SingleBlog'
//
export default [
	{
		path:"/",
		name: 'ShowBlogs',
		component:ShowBlogs
	},
	{
		path:"/AddBlog",
		name: 'AddBlog',
		component:AddBlog
	},
	{
		path:"/blog/:id",
		component:SingleBlog
	},
	{
		path:"/single",
		component:SingleBlog
	}
]

