<template>
	<div>
		<nav>
		<ul>
			<li>
				<router-link to="/" exact>我的博客</router-link>
				<router-link to="/AddBlog" exact>写博客</router-link>
			</li>
		</ul>
		</nav>
	</div>
	
</template>

<script>
	export default{
		name:"blog-header"
	}
</script>

<style scoped>
	ul{
		text-align: center;
		list-style-type: none;
	}
	li{
		display: inline-block;
		margin: 0 10px;
	}
	a{
		color: #FFFFFF;
		text-decoration: none;
		border-radius: 5px;
		padding: 12px;
	}
	nav{
		background: crimson;
		padding: 30px 0;
		margin-bottom: 40px;
	}
	.router-link-active{
		background: rgba(255,255,255,0.8);
		color: #444;
	}
</style>


