<template>
	<div id="single-blog">
		<div v-for="item in blog">
		<div v-show="item.id == id" class="single-blog" v-on:click="ddd">
		<h1>{{item.title}}</h1>
		<article>{{item.body}}</article>
		</div>
		</div>
	</div>
</template>

<script>
export default {
  name: 'single-blog',
  data(){
  	return{
  		id:1,
  		blog:{}
  	}
  },
// activited (){
//	  console.log('单独页面activited');
//},
  created(){
  	this.id = this.$route.params.id
  	this.$http.get('./..static/posts.json')
  	.then(function(data){
//		console.log(data.body)
		this.blog = data.body;
  	})
  },
  methods:{
	  ddd(){
		  console.log('当前文章id：' +this.$route.params.id)
	  }
  }
 }
</script>

<style>
	#single-blog{
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background: #eee;
		border: 1px dotted #EEEEEE;
	}
</style>


